.form {
  padding: 20px 0px;
  background-image: linear-gradient(180deg, #dfedf0, #e6dada);
  // border: 1px solid;
  display: flex;
  justify-content: center;
  .formTag {
    display: flex;
    flex-direction: column;
    align-items: center;
    // border: 1px solid red;
    width: 50vw;
    .formHeading {
      font-weight: bold;
      color: #017969;
      font-size: 3.2vw;
    }
    .lostBtn {
      margin-top: 10px;
      background-color: #00ce9f;
      color: white;
      border: 1px solid #00ce9f;
      border-radius: 5px;
      padding: 20px 0px;
      width: 100%;
    }
    .selectInputDiv {
      // border: 2px solid yellow;
      padding: 10px 0px;
      width: 100%;
      .selectTitle {
        margin: 5px 0px;
        color: rgb(1, 121, 105);
        font-size: 1.5vw;
        font-weight: 600;
        // border: 1px solid red;
      }
      .selectInput {
        margin-top: 15px;
        height: 50px;
      }
      .selectPara {
        margin: 15px 0 0 0;
        font-weight: 600;
        color: red;
      }
      .selectPara:nth-of-type(3) {
        margin: 0px;
      }
      .picture {
        display: flex;
        // border: 1px solid;
        .img {
          width: 15vw;
        }
        .imgPolicy {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          // border: 2px solid red;
          width: 100%;
          .imgPolicyOl {
            line-height: 2.3vw;
            margin-bottom: 0;
            font-weight: bold;
            font-size: 1.2vw;
            color: #00ce9f;
          }
        }
      }
    }
    .disableBtn {
      border: none;
      cursor: not-allowed;
      background-color: #d3d3d3;
      color: #ffffff;
    }
    .checkboxDiv {
      display: flex;
      align-items: center;
      .checkbox {
        margin-left: 10px;
      }
    }
    .checkboxPara {
      margin: 0;
      font-weight: 500;
      color: rgb(1, 121, 105);
      font-size: 1vw;

      // border: 1px solid red;
    }
    // Full Name Sub Title
    .nameSubTitle {
      font-size: 0.9vw;
    }

    .nextBtn {
      margin-top: 10px;
      text-align: center;
      align-self: flex-end;
      text-decoration: none;
      background-color: #00ce9f;
      color: white;
      border: 1px solid #00ce9f;
      border-radius: 5px;
      padding: 10px 15px;
      &:hover {
        background-color: #00b68b;
      }
    }
    .nextBtnDisable {
      margin-top: 10px;
      text-align: center;
      align-self: flex-end;
      text-decoration: none;
      background-color: lightgrey;
      color: white;
      // border: 1px solid #00ce9f;
      border-radius: 5px;
      padding: 10px 15px;
      cursor: not-allowed;
    }
    .submitBtn {
      margin-top: 10px;
      text-align: center;
      text-decoration: none;
      background-color: #00ce9f;
      color: white;
      transition: all 1s;
      border: 1px solid #00ce9f;
      border-radius: 5px;
      padding: 10px 15px;
      // width: 10%;
      &:hover {
        background-color: #00b68b;
      }
    }
  }
}

// Responsive
@import "../Responsive/formResponsive/formResponsive";
